import { TooltipServiceEvents } from './tooltipServiceEvents';
import { version } from './metadata';
import { BaseElement, booleanConverter, defineElement, html, property } from '@umetrics/sartorius-ui-base';
import {
    TooltipServiceDelay,
    TooltipServiceLocation,
    TooltipServiceProperties,
    TooltipServiceTrigger,
} from './tooltipServiceProperties';
import { tooltipServiceStyles } from './tooltipServiceStyle';
import { Timer } from './timer';
import { cancelIdleCallback, requestIdleCallback } from '@umetrics/sartorius-ui-utils';

/**
 * TooltipServiceOptions
 *
 * root: The node with children to be moved between service and tooltip. Ie the tooltip content.
 * object: The object to tooltip, like for example a button reference.
 * location: Preferred tooltip location [see TooltipServiceLocation]
 *
 * hideCallback : Callback function called when tooltip is closed
 * showCallback : Callback function called when tooltip is opened
 */
export interface TooltipServiceOptions {
    root: HTMLElement;
    object: HTMLElement;
    location: TooltipServiceLocation;
    ownWidth: boolean;
    delay: TooltipServiceDelay;

    hideCallback?: null | (() => void);
    showCallback?: null | (() => void);
}

@defineElement({ tagName: 'tooltip-service', styles: tooltipServiceStyles, version })
export class TooltipService extends BaseElement implements TooltipServiceProperties {
    // Static members

    // Public members
    @property({ type: Object, attribute: false })
    public object: HTMLElement | null = null;
    @property({ type: String, reflect: true, attribute: 'object-id' })
    public objectId: string | null = null;

    @property({ type: String, reflect: true, attribute: 'trigger-show' })
    public triggerShow: TooltipServiceTrigger = 'mouseover';
    @property({ type: String, reflect: true, attribute: 'trigger-hide' })
    public triggerHide: TooltipServiceTrigger = 'mouseout';

    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    public show = false;
    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    public disabled = false;
    @property({ type: TooltipServiceLocation, reflect: true })
    public location = TooltipServiceLocation.Auto;
    @property({ type: Boolean, reflect: true, converter: booleanConverter, attribute: 'own-width' })
    public ownWidth = false;

    @property({ type: TooltipServiceDelay, reflect: true })
    public delay = TooltipServiceDelay.Short;

    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    public hoverable = false;

    // Private members
    private _unshowHandle?: Timer;
    private _requestIdleCallback;
    // Protected members

    constructor() {
        super();

        this._onTriggerShow = this._onTriggerShow.bind(this);
        this._onTriggerHide = this._onTriggerHide.bind(this);
    }

    public static show(options: TooltipServiceOptions) {
        TooltipService._dispatchEvent(TooltipServiceEvents.Show, {
            ...options,
        });
    }
    public static hide(service) {
        TooltipService._dispatchEvent(TooltipServiceEvents.Hide, { root: service });
    }

    disconnectedCallback() {
        if (this.show) {
            // Dispatch hide incase tooltip is showing while disconnect from dom.
            this.show = false;
        }
        super.disconnectedCallback();
    }

    protected async firstUpdated(_changedProperties) {
        super.firstUpdated(_changedProperties);

        // Let browser paint before scheduling another update
        await new Promise((r) => setTimeout(r, 0));

        if (this.objectId && !this.object && this.parentElement) {
            this.object = this.parentElement.querySelector(`#${this.objectId}`);
        }
        if (this.show && this.triggerHide !== 'none') {
            this.object?.addEventListener(this.triggerHide, this._onTriggerHide, { once: true });
        } else if (!this.show && this.triggerShow !== 'none') {
            this.object?.addEventListener(this.triggerShow, this._onTriggerShow, { once: true });
        }

        this.addEventListener(TooltipServiceEvents.Hover, () => {
            if (this._unshowHandle) {
                this._unshowHandle.pause();
            }
        });
        this.addEventListener(TooltipServiceEvents.Unhover, () => {
            if (this._unshowHandle) {
                this._unshowHandle.resume();
            }
        });
    }

    protected update(changedProperties) {
        super.update(changedProperties);
        this._updateShow(changedProperties);
        this._updateObjectId(changedProperties);
        this._updateObject(changedProperties);
        this._updateTriggerShow(changedProperties);
        this._updateTriggerHide(changedProperties);
    }

    protected render() {
        return html`<slot></slot>`;
    }

    private static _dispatchEvent = (name, detail) => {
        window.dispatchEvent(
            new CustomEvent(name, {
                detail: {
                    ...detail,
                },
            })
        );
    };

    private _updateTriggerHide(changedProperties) {
        const prevTriggerHide = changedProperties.get('triggerHide');
        if (prevTriggerHide !== undefined && prevTriggerHide !== this.triggerHide && this.show) {
            if (prevTriggerHide !== 'none') {
                this.object?.removeEventListener(prevTriggerHide, this._onTriggerHide);
            }
            if (this.triggerHide !== 'none') {
                this.object?.addEventListener(this.triggerHide, this._onTriggerHide, { once: true });
            }
        }
    }

    private _updateTriggerShow(changedProperties) {
        const prevTriggerShow = changedProperties.get('triggerShow');
        if (prevTriggerShow !== undefined && prevTriggerShow !== this.triggerShow && !this.show) {
            if (prevTriggerShow !== 'none') {
                this.object?.removeEventListener(prevTriggerShow, this._onTriggerShow);
            }
            if (this.triggerShow !== 'none') {
                this.object?.addEventListener(this.triggerShow, this._onTriggerShow, { once: true });
            }
        }
    }

    private _updateObject(changedProperties) {
        const prevObject = changedProperties.get('object');
        if (prevObject !== undefined) {
            if (this.show && this.triggerHide !== 'none') {
                if (prevObject !== null) {
                    prevObject.removeEventListener(this.triggerHide, this._onTriggerHide);
                }
                this.object?.addEventListener(this.triggerHide, this._onTriggerHide, { once: true });
            } else if (!this.show && this.triggerShow !== 'none') {
                if (prevObject !== null) {
                    prevObject.removeEventListener(this.triggerShow, this._onTriggerShow);
                }
                this.object?.addEventListener(this.triggerShow, this._onTriggerShow, { once: true });
            }
        }
    }

    private _updateObjectId(changedProperties) {
        const prevObjectId = changedProperties.get('objectId');
        if (prevObjectId !== undefined) {
            this.object =
                this.objectId && this.parentElement ? this.parentElement.querySelector(`#${this.objectId}`) : null;
        }
    }

    private _updateShow(changedProperties) {
        const prevShow = changedProperties.get('show');
        if (prevShow !== undefined && prevShow !== this.show) {
            this._toggle();
            if (this.show && this.triggerHide !== 'none') {
                this.object?.addEventListener(this.triggerHide, this._onTriggerHide, { once: true });
            } else if (!this.show && this.triggerShow !== 'none') {
                this.object?.addEventListener(this.triggerShow, this._onTriggerShow, { once: true });
            }
        }
    }

    private _toggle() {
        if (!this.disabled) {
            if (this.show) {
                if (!this._requestIdleCallback) {
                    this._requestIdleCallback = requestIdleCallback(() => {
                        this._requestIdleCallback = undefined;
                        TooltipService.show({
                            root: this,
                            object: this.object as HTMLElement,
                            location: this.location,
                            ownWidth: this.ownWidth,
                            delay: this.delay,
                            hideCallback: () => {
                                this.show = false;
                                this._dispatchSelfEvent(TooltipServiceEvents.Closed);
                            },
                            showCallback: () => {
                                this._dispatchSelfEvent(TooltipServiceEvents.Opened);
                            },
                        });
                    });
                }
                window.addEventListener('scroll', this._onTriggerHide);
                return;
            } else {
                window.removeEventListener('scroll', this._onTriggerHide);
            }
        }
        if (this._requestIdleCallback) {
            cancelIdleCallback(this._requestIdleCallback);
            this._requestIdleCallback = undefined;
        }
        TooltipService.hide(this);
    }

    private _onTriggerShow() {
        if (this._unshowHandle) {
            this._unshowHandle.pause();
            this._unshowHandle = undefined;
        }
        if (!this.show) {
            this.show = true;
        }
        if (this.triggerHide !== 'none') {
            this.object?.addEventListener(this.triggerHide, this._onTriggerHide, { once: true });
        }
    }

    private _onTriggerHide() {
        if (this.show) {
            if (this.triggerShow !== 'none') {
                this.object?.addEventListener(this.triggerShow, this._onTriggerShow, { once: true });
            }
            if (this.hoverable) {
                this._unshowHandle = new Timer(() => {
                    this.show = false;

                    this._unshowHandle = undefined;
                }, 100);
            } else {
                this.show = false;
            }
        }
    }

    private _dispatchSelfEvent(name: TooltipServiceEvents) {
        return this.dispatchEvent(
            new CustomEvent(name, {
                cancelable: true,
                bubbles: true,
                composed: true,
            })
        );
    }
}
